.contact-section {
    overflow: hidden;
    padding: 0 0 1.5rem;
    .background-regular {
        width: 100%;
        height: 0%;
    }
    .background-wide {
        width: 100%;
        
        height: 400%;
    }
    .background {
        position: absolute;
        transition: 0.5s;
        transition-delay: 0.1s;
        // height: 100%;
        background: rgb(230,0,0);
        background: linear-gradient(90deg, rgba(32,191,85,1) 0%, rgba(1,186,239,1) 10%, rgba(219,203,216,1) 25%, rgba(46,64,87,1) 40%, rgba(164,6,6,1) 55%, rgba(217,131,36,1) 70%, rgba(251,176,52,1) 85%, rgba(255,221,0,1) 100%);

    }

    .content-container {
        background-color: rgb(64,64,64);
        min-height: 50vh;
        width: 100%;
        height: 100%;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        
        h2 {
            text-align: center;
            font-size: 2rem;
            margin: 1rem;
            span {
                color: red;
                cursor: pointer;
            }
        }
        h3 {
            font-size: 1.5rem;
        }
        .call-to-action {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            color: white;
            flex-grow: 1;
        }

        .contact-info-container {
            display: flex;
            flex-grow: 1;
            width: 100%;
            
            
        }

        li {
            color:white;
            font-size: 1.2rem;
            display: flex;
            margin-bottom: 1rem;
            svg {
                margin-right: 1rem;
            }
        }
        .contact-info {
            min-width: 50%;
            display: flex;
            justify-content: center;
            
            margin: 1rem 0rem;
        }
        .contact-btns {
            margin: 1rem 0rem;
            display: flex;
            min-width: 50%;
            justify-content: center;
            li {
                cursor: pointer;
                
                &:hover {
                    transition: 0.2s;
                    color: red;
                }

            }
        }
    }
}



@media screen and (max-width: 600px) {
    .contact-section .contact-info-container {
        flex-direction: column;
        .contact-info,
        .contact-btns {
            justify-content: flex-start;
        }
    }
}